.header {
  position: relative;
}

.container {
  position: relative;

  min-height: 1550px;

  background-image: url("../../images/header-nav.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.authorization_box {
  padding-top: 20px;
  margin-bottom: 260px;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 35px;
  margin-right: 20px;
}

.sign_in {
  padding: 20px 40px;
  background: #5d6afb;
  border-radius: 8px;
  text-decoration: none;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 152%;
  color: #ffffff;
  border: 2px solid transparent;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.sign_in:hover {
  background: #5d6afb00;
  border-color: #5d6afb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.sign_up {
  composes: sign_in;
}

.logo {
  -o-object-fit: contain;
  object-fit: contain;
  margin-left: 30px;
}

.title {
  margin: 100px 0 0 0;
  padding-left: 105px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 54px;
  line-height: 66px;
  color: #ffffff;
}

.form_container {
  margin-top: -950px;
  background-image: url("../../images/header-form.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
  min-height: 2040px;
}

.form {
  position: absolute;
  bottom: 145px;
  right: 40px;
  z-index: 1;
  height: 893px;
  margin: 0 0 0 auto;

  max-width: 770px;
  width: 100%;
  background: rgba(89, 38, 234, 0.8);
  border: 1px solid #000000;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 80px;

  -webkit-transform: translateY(-50%);

  -ms-transform: translateY(-50%);

  transform: translateY(-50%);
}

.form_title {
  width: 100%;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 45px;
  line-height: 152%;
  text-align: center;
  color: #ffffff;
  margin-top: 80px;
  margin-bottom: 30px;
}

.input {
  font-family: Montserrat;
  max-width: 690px;
  width: 100%;
  height: 97px;
  background: #fffafa;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 13px;
  margin-bottom: 60px;
  padding-top: 30px;
  padding-bottom: 32px;
  padding-left: 85px;
  margin-left: 40px;
  margin-right: 40px;
  background-repeat: no-repeat;
  background-position: 50px center;
  background-size: 28px 28px;
  border: none;
  outline: none;
  font-size: 25px;
}

.input:first-of-type {
  background-size: 27px 27px;
  background-image: url(../../images/input_name-icon.svg);
}

.input:nth-of-type(2) {
  background-image: url(../../images/input_number-icon.svg);
}

.input:last-of-type {
  background-image: url(../../images/input_email-icon.svg);
}

.form_button {
  margin: 0 auto;
  max-width: 690px;
  width: 100%;
  height: 97px;
  background: #d43355;
  border: 1px solid #000000;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 48.5px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 133%;
  text-align: center;
  color: #ffffff;
  margin-bottom: 30px;
  margin-left: 40px;
  margin-right: 40px;
  cursor: pointer;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.form_button:hover {
  border: 3px solid #d43355;
  background: #d4335600;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

